import React, { useContext } from 'react';
import { WebchatContext } from '@botonic/react';
import type { FC } from 'react';
import clsx from 'clsx';
import i18next from 'i18next';
import styles from './CustomNumber.module.css';
import commonStyles from '../../styles/common.module.css';
import { toLowerCase } from '../../utils/helper';
import ErrorMessage from '../ErrorMessage';

interface Props {
  label: string;
  onChange: () => void;
  onBlur: () => void;
  value: string;
  isError: boolean;
  required: boolean;
}

export const CustomNumber: FC = React.forwardRef((props: Props, ref) => {
  const { label, onChange, onBlur, value, isError, required } = props;

  const {
    webchatState: { theme },
  } = useContext(WebchatContext);

  return (
    <div className={styles.customField}>
      <div className={commonStyles.placeholder} />
      <div className={styles.wrapper}>
        <div className={styles.labelWrapper}>
          <p
            className={styles.label}
            style={{
              fontSize: theme?.initialScreen?.customFieldLabelFontSize,
              fontFamily: theme?.initialScreen?.customFieldLabelFontFamily,
              color: theme?.initialScreen?.customFieldLabelColor,
            }}
          >
            {label}
            {required ? <span className={commonStyles.isRequired}>*</span> : null}
            <div className={styles.helper}>
              {theme?.initialScreen?.customFieldPhoneDescription ??
                i18next.t('customFieldPhoneDescription')}
            </div>
          </p>
        </div>

        <input
          ref={ref}
          value={value || ''}
          autoComplete="off"
          className={clsx(styles.input, {
            [commonStyles.isError]: isError,
          })}
          id="input"
          onChange={onChange}
          onBlur={onBlur}
          placeholder={i18next.t('customFieldPlaceholder', {
            type: theme?.initialScreen?.customFieldInputPlaceholder || 'Type',
            label: toLowerCase(label),
            interpolation: { escapeValue: false },
          })}
          data-qa="test-number-field"
          style={{
            fontSize: theme?.initialScreen?.customFieldInputFontSize,
            fontFamily: theme?.initialScreen?.customFieldInputFontFamily,
            color: theme?.initialScreen?.customFieldInputColor,
            backgroundColor: theme?.initialScreen?.customFieldInputBackgroundColor,
          }}
        />
        {isError && (
          <ErrorMessage
            message={
              theme?.initialScreen?.customFieldPhoneInvalidFormatMessage ??
              i18next.t('errorPhoneNumberWarning')
            }
          />
        )}
      </div>
      <div className={commonStyles.placeholder} />
    </div>
  );
});
